.MobileBar {
    position: fixed;
    background-color: #3E454C;
    left: -20%;
    top: 0;
    width: 50px;
    height: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.mobile-bar-trigger {
    cursor: pointer;
    font-size: 1.5em;
    color: #FFF6E5;
}

.mobile-logo {
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
    width: auto;
    margin-top: -20px;
    height: 50px;
}

.mobile-triggered {
    left: 300px !important;
}

@media (max-width: 1024px){
    .MobileBar {
        left: 0;
    }
}

@media (max-width: 500px){
    .MobileBar {
        width: 25px;
        left: 0;
    }

    .mobile-bar-trigger {
        font-size: 1em;
    }
}


