.Title {
    position: absolute;
    width: 75%;
    height: 10%;
    top: 0;
    left: 25%;
    margin: 0;
    padding: 0;
}

.title-text {
    font-size: 2.5em;
    margin-top: 10px;
    margin-bottom: 5px;
}

.source-link {
    color: #3E454C;
    margin-left: 5%;
    font-size: 1em;
    font-style: normal;
    text-decoration: none;
}

.source-link:hover {
    color: #FF7F66;
}

@media (max-width: 1024px){
    .Title {
        left: 13%;
        font-size: .8em;
        width: 85%;
    }
}

@media (max-width: 750px){
    .Title {
        left: 13%;
        font-size: .6em;
        width: 85%;
    }
}

@media (max-width: 600px){
    .Title {
        left: 13%;
        font-size: .5em;
        width: 85%;
    }
}

@media (min-height: 900px){

}


