.Sidebar {
    position: fixed;
    width: 20%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #3E454C;
    margin: 0;
    padding: 0;
    z-index: 1;
}

.sidebar-title {
    text-align: left;
    background-color: #2185C5;
    height: 10%;
    width: calc(100%);
    position: absolute;
    top: 0;
    left: 0;
    border-bottom: 10px solid black;
}

.sidebar-classes {
    position: absolute;
    width: 100%;
    top: calc(10% + 15px);
    left: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    overflow: hidden;
}

.sidebar-classname {
    height: 50px;
    width: 100%;
    margin: 0;
    padding: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    color: #FFF6E5;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;    
}

.sidebar-classname > div{
    margin-left: 10%;
    font-weight: bold;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;    
}

.sidebar-classname:hover {
    cursor: pointer;
    background-color: #FF7F66;
}

.sidebar-delimiter {
    height: 50px;
    width: 100%;
    font-size: 1.2em;
    color: #FFF6E5;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
}

.sidebar-delimiter:hover{
    background-color: #2185C5;    
}

.sidebar-delimiter > div{
    margin-left: 5% !important;
}

.logo-link {
    width: 100%;
    height: 100% !important;
    margin: 0;
    height: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.logo {
    width: 60%;
    height: auto;
    margin-right: 10%;
}

.sidebar-triggered {
    left: 0 !important; 
}

@media (max-width: 1024px){
    .Sidebar {
        left: -300px;
        width: 300px;
    }
}

@media (max-width: 1250px){
    .sidebar-classname {
        font-size: .8em;  
    }
}




