.Content {
    position: absolute;
    width: 60%;
    height: auto;
    top: 12%;
    left: 30%;
    color: black;
    padding-bottom: 20px;
}

.function {
    font-style: italic;
    font-weight: 100;
    font-size: .8em;
    font-family: 'Garamond';
}

.class-container {
    margin-left: 5%;
}

.class-content {
    margin: 0;
    padding: 0;    
    font-size: 1.2em;
}

.class-docstring {
    margin-left: 5%;
    width: 95;
}

.class-content > h4 {
    background-color: #FFF4E0;
    font-weight: 700;
    overflow: hidden;
    overflow-wrap: break-word;
}

.markdown {
    width: 70%;
    margin-left: 2%;
    font-size: .8em;
}

code {
    font-style: normal;
    background-color: #3E454C !important;
}

@media (max-width: 1024px){
    .Content {
        left: 15%;
        top: 10%;
        width: 80%;
    }

    .class-content {
        margin: 0;
        padding: 0;    
        font-size: 1.2em;
    }

    .class-content > h4 {
        background-color: #FFF4E0;
        font-weight: 700;
        overflow: hidden;
        overflow-wrap: break-word;
        font-size: 1.2em;
    }
    
}

@media (max-width: 750px){
    .Content {
        left: 15%;
        top: 10%;
        width: 80%;
    }

    .class-title {
        font-size: 1.5em;
    }

    .class-docstring {
        font-size: .8em;
    }

    .class-content {
        margin: 0;
        padding: 0;    
        font-size: 1em;
    }

    .class-content > h4 {
        background-color: #FFF4E0;
        font-weight: 700;
        overflow: hidden;
        overflow-wrap: break-word;
        font-size: 1em;
    }
    
}

@media (max-width: 600px){   
    
    .class-title {
        font-size: 1em;
    }

    .class-docstring {
        font-size: 0.6em;
    }

    .class-content > h4 {
        background-color: #FFF4E0;
        font-weight: 700;
        overflow: hidden;
        overflow-wrap: break-word;
        font-size: .8em;
    }
    
}

@media (min-height: 900px){
    .Content {
        top: 10%;
    }

    .content-docstring {
        font-size: 1em;
    }
}


